.loader{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px dashed rgb(133, 29, 29);
    animation: rotate 1s infinite linear;
}

@keyframes rotate {
    from{
        transform: rotate(0deg) scale(0.6);
    }
    to{
        transform: rotate(360deg) scale(1.2);
    }
}